/** UI **/
.no-webp .inicio {
  background-image: url(../img/casa13.webp);
}
@media (min-width: 768px) {
  .no-webp .inicio {
    background-image: url(../img/casa9.webp);
  }
}

.webp .inicio {
  background-image: url(../img/casa13.webp);
}
@media (min-width: 768px) {
  .webp .inicio {
    background-image: url(../img/casa9.webp);
  }
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  background-attachment: fixed;
}
.header.inicio {
  background-position: center;
  background-size: cover;
  background-blend-mode: difference;
  min-height: 58rem;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 41rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.home {
  text-align: center;
}
@media (min-width: 1024px) {
  .home {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    margin-top: 8rem;
  }
}
.home img {
  display: none;
}
@media (min-width: 1024px) {
  .home img {
    width: 70%;
    display: block;
    margin: 0 auto;
    margin-right: 5rem;
    filter: drop-shadow(2px 4px 6px #000000);
    animation-name: bounce;
    animation-timing-function: ease;
  }
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(0);
  }
}
@media (min-width: 1024px) {
  .home .texto-home {
    width: 120%;
  }
}
.home .texto-home .texto2 {
  display: none;
}
@media (max-width: 768px) {
  .home .texto-home .texto1 {
    display: none;
  }
  .home .texto-home .texto2 {
    display: block;
    color: white;
  }
}
.home .texto-home p {
  display: none;
}
@media (min-width: 768px) {
  .home .texto-home p {
    display: block;
    color: #FFFFFF;
    width: 80%;
    margin: 0px auto;
    margin-top: -2rem;
    margin-bottom: 2rem;
    font-size: 1.4rem;
  }
}
.home .texto-home .escritura {
  white-space: nowrap;
  border-right: 4px solid;
  width: 53ch;
  animation: typing 3s steps(50), blink 0.5s infinite step-end alternate;
  overflow: hidden;
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}
.home .texto-home h1 {
  padding: 0.5rem;
  margin-top: 5rem;
  font-size: 2.5rem;
  color: #FFFFFF;
  font-weight: 800;
}
@media (min-width: 768px) {
  .home .texto-home h1 {
    font-size: 3rem;
  }
}
.home .texto-home span {
  color: rgb(194, 64, 64);
  text-shadow: 0px 0px 10px #000000;
}
.home .texto-home .boton {
  width: 80%;
  margin: 0px auto;
  margin-top: 0.5rem;
  transition: 0.3s ease-in-out;
  overflow: hidden;
}
@media (min-width: 768px) {
  .home .texto-home .boton {
    width: 50%;
  }
}
.home .texto-home .boton:hover {
  -webkit-transform: scale(0.95);
  transform: scale(0.95);
}

#contenedor_carga {
  background-color: white;
  height: 100%;
  width: 100%;
  position: fixed;
  transition: all 0.9s cubic-bezier(0.075, 0.82, 0.165, 1);
  z-index: 100000;
}

#carga {
  border: 5px solid rgb(194, 64, 64);
  border-top: 10px solid #eb4335; /* red */
  border-bottom: 10px solid #a3a3a3; /* blue */
  border-right: 10px solid #fbbc05; /* yellow */
  border-left: 10px solid #34a853; /* green */
  height: 100px;
  width: 100px;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  animation: girar 1.5s linear infinite;
}

@keyframes girar {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.contenido-header .contenedor-bajar {
  display: none;
}

@media (min-width: 1032px) {
  .contenido-header .contenedor-bajar {
    display: block;
    font-size: 3rem;
    color: white;
    text-align: center;
    margin: 0px auto;
    margin-top: 0rem;
    cursor: pointer;
    animation: slide1 1s ease-in-out infinite;
  }
}
@keyframes slide1 {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0px, 15px);
  }
}
.share {
  display: none;
}

@media (min-width: 1032px) {
  .share {
    display: block;
    width: 5%;
    position: fixed;
    top: calc(50% - 125px);
    z-index: 10;
  }
  .share .contenedor-share {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
  }
  .share .contenedor-share a {
    width: 100%;
  }
  .share .contenedor-share i {
    font-size: 2rem;
    color: white;
    padding: 2rem 0;
    transition: all 0.3s ease-in-out;
  }
  .share .contenedor-share .bar-icono {
    width: 100%;
    text-align: center;
    transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
  .share .contenedor-share .bar-icono:hover {
    width: 150%;
  }
  .share .contenedor-share .bar-icono:hover i {
    transform: rotate(-40deg);
  }
  .share .contenedor-share .facebook-logo {
    background-color: rgb(35, 94, 202);
  }
  .share .contenedor-share .twitter-logo {
    background-color: rgb(0, 162, 255);
  }
  .share .contenedor-share .email-logo {
    background-color: rgb(221, 19, 12);
  }
  .share .contenedor-share .link-logo {
    background-color: rgb(255, 255, 255);
  }
}
.footer {
  padding: 2.5rem;
  padding-bottom: 3rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding: 2.5rem;
}
.footer .copyrigth {
  color: #FFFFFF;
  text-align: center;
  margin-top: 3rem;
  font-size: 1.8rem;
}

@media (max-width: 768px) {
  .n-footer {
    display: none;
  }
}
.social-footer ul {
  list-style: none;
  display: flex;
  gap: 3rem;
  justify-content: center;
  padding: 0;
}
.social-footer ul li a i {
  font-size: 3.2rem;
  color: white;
  transition: 0.5s all ease-in-out;
}
.social-footer ul li a i:hover {
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}
.social-footer p {
  color: white;
}

.slogan {
  text-align: center;
  color: white;
  font-size: 1rem;
  margin-top: -1rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra img {
  width: 25rem;
  filter: drop-shadow(0px 0px 3px black);
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility linear, opacity 0.5s linear;
  margin-bottom: -15rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    margin-bottom: 0;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-shadow: 0px 0px 5px black;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
    font-size: 2rem;
    transition: 0.5s ease-in-out;
    background-image: linear-gradient(to right, #54b3d6, #54b3d6 50%, rgb(255, 255, 255) 50%);
    background-size: 200% 100%;
    background-position: -100%;
    display: inline-block;
    padding: 5px 0;
    position: relative;
    -webkit-background-clip: text;
    transition: all 0.3s ease-in-out;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:before {
    content: "";
    background: white;
    display: block;
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 0;
    height: 3px;
    transition: all 0.3s ease-in-out;
  }
  .navegacion a:hover {
    background-position: 0;
  }
  .navegacion a:hover::before {
    width: 100%;
  }
}

