.carousel__elemento {
  text-align: center;
  padding: 1rem;
}

.carousel__elemento a {
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.carousel__elemento a:hover {
  box-shadow: 0px 0px 10px #999;
}

.alerta {
  padding: 1rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin: 2rem 0;
  color: white;
}
.alerta.error {
  background-color: rgb(194, 64, 64);
}

.boton-enviar3 {
  margin: 0px auto;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .boton-enviar3 {
    width: 30%;
  }
}

.imagen-admin {
  width: 90% !important;
}