.contenedor-anuncios{
    @include grid(3, 2rem);
}
@media (max-width: ($tablet)){
    .contenedor-anuncios{
        @include grid(2, 2rem);
    }
}
@media (max-width: ($telefono)){
    .contenedor-anuncios{
        @include grid(1, 2rem);
    }
}


.anuncio{
    margin-bottom: 2rem;
    background-color: lighten($gris, 5);
    transition: 0.3s ease-in-out;
    overflow: hidden;

    &:hover{
        box-shadow: 0px 0px 10px #d8d8d8;
    }

    .contenido-anuncio{
        padding: 2rem;

        .boton{
            text-transform: uppercase;
            transition: 0.3s ease-in-out;
            overflow: hidden;
            &:hover{
                -webkit-transform:scale(0.95);transform:scale(0.95);
            }

        }

    }
    h3{
        font-size: 2.8rem;
        color: #525252;
    }
    h3,p{
        margin: 0;
        text-align: center;
    }
    p{
        font-weight: 500;
        color: #999;
    }
    .precio{
        color: $grisclaro;
        font-size: 1.3rem;
    }
    img{
        width: 40%;
        margin: 0px auto;
        margin-top: 5rem;
        filter: drop-shadow(0px 0px 6px #dbdbdb);
        transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);
        &:hover{
            -webkit-transform:scale(0.95);transform:scale(0.95);
        }
    }
    .big{
        width: 37.5%;
    }
}

.ver-todas{
    display: flex;
    justify-content: flex-end;
}