.footer{
    padding: calc($separacion / 2) ;
    padding-bottom: 3rem;
    background-color: $grisOscuro;
    margin-bottom: 0;
    .contenido-footer{
        padding: calc($separacion / 2);
        
    }
    .copyrigth{
        color: $blanco;
        text-align: center;
        margin-top: 3rem;
        font-size: 1.8rem;
    }
}

@media(max-width:($tablet)){
    .n-footer{
        display: none;
    }
    
}

.social-footer{
    ul{
        list-style: none;
        display: flex;
        gap: 3rem;
        justify-content: center;
        padding: 0;

        li a i{
            font-size: 3.2rem;
            color: white;  
            transition: .5s all ease-in-out;   
            &:hover{
                -webkit-transform:scale(0.9);transform:scale(0.9);
            } 
        }
    }
    p{
        color: white;
    }
}
.slogan{
    text-align: center;
    color: white;
    font-size: 1rem;
    margin-top: -1rem;
}