.testimonial{
    background-color: #c29d40;
    font-size: 1.9rem;
    padding: 1.5rem;
    color: $blanco;
    border-radius: 1rem;
    blockquote{
        font-size: 1.8rem;
        padding-left: 5rem;
        position: relative;
        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
        }

    }
    p{
        text-align: right;
        color: $blanco;
    }
}