.no-webp .inicio{
    @include tablet{
        background-image: url(../img/casa9.webp);
    }
    background-image: url(../img/casa13.webp);

}

.webp .inicio{
    @include tablet{
        background-image: url(../img/casa9.webp);
    }
    background-image: url(../img/casa13.webp);
    
    
    
}
.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    background-attachment: fixed;


    &.inicio{
        background-position:center;
        background-size: cover;
        background-blend-mode: difference;
        min-height: 58rem;
        .contenido-header{
            @include tablet{
                min-height: 41rem;
            }

        }
        
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.home{
    text-align: center;
    @include desktop{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 5rem;
        margin-top: 8rem;
    }
    img{
        display: none;
        @include desktop{
            width: 70%;
            display: block;
            margin: 0 auto;
            margin-right: 5rem;
            filter: drop-shadow(2px 4px 6px $negro);
            animation-name: bounce;
            animation-timing-function: ease;
        }
        
        
        
    }
    @keyframes bounce {
        0%   { transform: translateY(0); }
        50%  { transform: translateY(-100px); }
        100% { transform: translateY(0); }
    }
    .texto-home{
        @include desktop{
            width: 120%;
        }
        .texto2{
            display: none;
        }
        @media(max-width:($tablet)){
            .texto1{
                display: none;
            }
            .texto2{
                display: block;
                color: white;
            }
        }
        
        p{
            @include tablet{
                display: block;
                color: $blanco;
                width: 80%;
                margin: 0px auto;
                margin-top: -2rem;
                margin-bottom: 2rem;
                font-size: 1.4rem;
            }
            display: none;
            
        }
        .escritura{
            white-space: nowrap;
            border-right: 4px solid;
            width: 53ch;
        
            animation: typing 3s steps(50), blink .5s infinite step-end alternate;
            overflow: hidden;
        }
        @keyframes typing {
            from { width: 0;}
        }
        
        @keyframes blink {
            50% { border-color: transparent;}
        }
        h1{
            @include tablet{
                font-size: 3rem;
            }
            padding: 0.5rem;
            margin-top: 5rem;
            font-size: 2.5rem;
            color: $blanco;
            font-weight: 800;
            
            
        }
        span{
            color: $rojo;
            text-shadow: 0px 0px 10px $negro;
            
        }
        .boton{
            @include tablet{
                width: 50%;
            }
            width: 80%;
            margin: 0px auto;
            margin-top: 0.5rem;
            transition: 0.3s ease-in-out;
            overflow: hidden;
            &:hover{
                -webkit-transform:scale(0.95);transform:scale(0.95);
            }
        }

    }
    
    
    
}

#contenedor_carga{
    background-color: white;
    height: 100%;
    width: 100%;
    position: fixed;
    transition:  all 0.9s cubic-bezier(0.075, 0.82, 0.165, 1);
    z-index: 100000;
  }
#carga{
    border: 5px solid $rojo;
    border-top: 10px solid #eb4335; /* red */
    border-bottom: 10px solid #a3a3a3; /* blue */
    border-right: 10px solid #fbbc05; /* yellow */
    border-left:10px solid #34a853; /* green */
    height: 100px;
    width: 100px;
    border-radius: 100%;

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    animation: girar 1.5s linear infinite;

    }
    @keyframes girar{
    from {
        
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }

    
}


.contenido-header .contenedor-bajar {
    display: none;
}

@media(min-width:1032px){
    .contenido-header .contenedor-bajar {
        display: block;
        font-size: 3rem;
        color: white;
        text-align: center;
        margin: 0px auto;
        margin-top: 0rem;
        cursor: pointer;
        animation: slide1 1s ease-in-out infinite;
    }
}




@keyframes slide1 {
    0%,
    100% {
      transform: translate(0, 0);
    }
  
    50% {
      transform: translate(0px, 15px);
    }
}


