.share{
    display: none;
}
@media(min-width:1032px){
    .share{
        display: block;
        width: 5%;
        position: fixed;
        top: calc(50% - 125px);
        z-index: 10;
    
        .contenedor-share{
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 80%;
            a{
                width: 100%;
            }
            i{
                font-size: 2rem;
                color: white;
                padding: 2rem 0;
                transition: all .3s ease-in-out;
            }
            .bar-icono{
                width: 100%;
                text-align: center;
                transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);
                &:hover{
                    width: 150%;
                    i{
                        transform: rotate(-40deg);
                    }
                }

            }
            .facebook-logo{
                background-color: rgb(35, 94, 202);
                

            }
            .twitter-logo{
                background-color: rgb(0, 162, 255);
            }
            .email-logo{
                background-color: rgb(221, 19, 12);
            }
            .link-logo{
                background-color: rgb(255, 255, 255);
            }
        
        }
        
    }
}


