.no-webp .imagen-blog{
    background-image: url(../img/proyecto2.png);

}

.webp .imagen-blog{ 
    background-image: url(../img/pasta.webp);
    
    
}
.proyectos{
    .imagen-blog{
        background-size: cover;
        background-repeat: no-repeat;
        background-clip: border-box;
        background-position: center;
    }
    
    
    .contenido-proyectos{
        @include tablet{
            display: grid;
            grid-template-columns: repeat(2,1fr);
            gap: 5rem;
            align-items: center !important;
            margin: 0px auto;
            
        }
        img{
            @include tablet{
                display: block;
                width: 90%;
                margin: 0px auto;
                border: 5px solid $blanco;
                cursor: pointer;
                transition: 0.5s all ease-in-out;

                &:hover{
                    filter: grayscale(1);
                }
            }
            display: none;
        }
        h3{
            font-weight: 100;
            font-size: 2.2rem;
        }
        p{
            font-weight: 100;
            font-size: 1.5rem;
        }

            
        .texto-proyectos{
            @include tablet{
                background-color: rgba(0, 0, 0, 0.404);
            }
            background-color: rgba(0, 0, 0, 0.5);
        }

    }
    

}

.imagen-trabajo{
    background-position: center;
}


.proyecto-productos{
    @include tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 5rem;
        align-items: center !important;
        margin: 0px auto;
        
    }
    img{
        border: 10px solid $blanco;
        box-shadow: 0px 0px 8px #999;
        
    }
    p{
        font-weight: 100;
        font-size: 1.8rem;
    }
}

.galeria{
    width: 90%;
    margin: 0px auto;
    margin-top: 3rem;
    .contenedor-galeria{
        @include tablet{
            margin-top: 5rem;
            @include grid(3,2rem);
        }
        .imagen-galeria{
            img{
                position: relative;
            }
            a{
                background-image: url(../img/casa4.webp);
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                display: block;
                height: 30rem;
                filter: grayscale(0.9);
                cursor: pointer;
                transition: 0.5s all ease-in-out;
    
                &:hover{
                    filter: grayscale(0);
                    -webkit-transform:scale(0.95);transform:scale(0.95);
                }

                
            }
            p{
                text-decoration: overline;
            }
            .image-1{
                background-image: url(../img/casa0.webp);
                background-position: top;
            }
            .image-2{
                background-image: url(../img/casa6.webp);
                background-position: top;
            }
            .image-4{
                background-image: url(../img/casa2.webp);
                background-position: top;
            }
            .image-5{
                background-image: url(../img/casa11.webp);
                background-position: top;
            }
            .image-6{
                background-image: url(../img/casa12.webp);
                background-position: center;
            }
        }

            

    }
}

/*carousel*/
.carousel__contenedor{
    position: relative;
}
.carousel__anterior,
.carousel__siguiente{
    position: absolute;
    display: none;
    font-size: 3rem;
    width: 3rem;
    height: 3rem;
    border: none;
    top: calc(50% - 40px);
    cursor: pointer;
    line-height: 3rem;
    text-align: center;
    background: none;
    color: $negro;
    opacity: 40%;
    transition: .3s all cubic-bezier(0.075, 0.82, 0.165, 1);

}

@include tablet{
    .carousel__anterior,
    .carousel__siguiente{
        position: absolute;
        display: block;
        font-size: 3rem;
        width: 3rem;
        height: 3rem;
        border: none;
        top: calc(50% - 40px);
        cursor: pointer;
        line-height: 3rem;
        text-align: center;
        background: none;
        color: $negro;
        opacity: 40%;
        transition: .3s all cubic-bezier(0.075, 0.82, 0.165, 1);
    
    }
}
@include tablet{
    .carousel__anterior{
        left: -30px;
    }
    .carousel__siguiente{
        right: -30px;
    }
}
.carousel__anterior{
    left: -30px;
}
.carousel__siguiente{
    right: -30px;
}

.carousel__lista{
    overflow: hidden;
}

.carousel__elemento{
    text-align: center;
    padding: 1rem;
    
}
.carousel__elemento a{
    transition: .3s all cubic-bezier(0.075, 0.82, 0.165, 1);
}
.carousel__elemento a:hover{
    box-shadow: 0px 0px 10px #999;
}