.title-nosotros{
    margin: 0;
}
.iconos-nosotros{
    margin-top: 5rem;
    @include grid(3,2rem);
    
}
.title-iconos{
    h2{
        text-align: center;
        &::after{
            margin: 0px auto;
        }
    }
    p{
        text-align: center;
    }
}
@media (max-width: ($tablet)){
    .iconos-nosotros{
        margin-top: 5rem;
        @include grid(2,2rem);
    }
}

@media (max-width: ($telefono)){
    .iconos-nosotros{
        margin-top: 5rem;
        @include grid(1,2rem);
    }
}


.icono{
    text-align: center;
    img{
        height: 10rem;

    }
    h3{
        text-transform: uppercase;
    }
    p{
        font-size: 1.5rem;
        width: 80%;
        margin: 0px auto;
        font-weight: 100;
    }
}