.no-webp .anuncios{
    background-image: url(../img/casa10.png);

}

.webp .anuncios{ 
    background-image: url(../img/casa10.webp);
    
    
}

.webp .banner-imagen{
    background-image: url(../img/banner-pintura.webp);
}

.webp .banner-material{
    background-image: url(../img/banner-material.webp);

}
.anuncios{
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    background-blend-mode: difference;   

}

.webp .imagen-material{
    background-image: url(../img/pasta2.webp);
    background-attachment: fixed;

}

.imagen-material{
    background-size: cover;
    background-position: center;
    margin-bottom: 5rem;
    padding: 15rem;
    filter: grayscale(1);
    .texto-contacto{
        background-color: rgba(0, 0, 0, 0.753);
        padding-right: 20rem;
        padding-left: 20rem;

        h2{
            font-style: italic;
            font-size: 3rem;
            text-shadow: 0px 0px 10px $negro;
        }
    }
}

.banner-imagen{
    margin-top: 4rem;
    margin-bottom: 4rem;
    @include tablet{
        padding: 22rem;
        overflow: hidden;
    }
    
}

.contenedor-pinturas{
    @include tablet{
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(2,1fr);

    }
    

    .anuncio{
        img{
            width: 60%;
            padding-top: 1rem;
        }

        p{
            width: 60%;
            margin: 0px auto;
            margin-bottom: 1rem;
        }
        input:not([type="submit"]),
        textarea,select{
            padding: 1rem;
            display: block;
            width: 100%;
            background-color: white;
            border: 2px solid rgb(235, 235, 235);
            border-radius: 0.5rem;
            margin-bottom: 2rem;

            
        }
        button[type="submit"]{
            width: 100%;
            padding: 1.5rem;
        }


    }
}
