.imagen-direccion{
    background-position: top;
    background-size: cover;
    padding: 3rem;
    margin-bottom: -3rem;

    .contenido-direccion{
        
        .iconos-contacto{
            ul{
                display: grid;
                grid-template-columns: repeat(4,1fr);
                align-items: center;
                list-style: none;
                gap: 5rem;
                margin-top: 5rem;
                margin-bottom: 5rem;
                li{
                    display: flex;
                    align-items: center;
                    gap: 2rem;
                    border-right: 2px solid #dbdbdb;
                    &:last-child{
                        border-right: none
                    }

                    i{
                        font-size: 4rem;
                    }
                    .informacion-icono{
                        h3{
                            font-size: 2rem;
                            margin: 0;
                            text-align: left;
                        }
                        p{
                            margin: 0;
                            font-size: 1.2rem;
                        }
                    }

                }
            }
            @media(max-width: ($desktop)){
                ul{
                    display: grid;
                    grid-template-columns: repeat(3,1fr);
                    align-items: center;
                    list-style: none;
                    gap: 5rem;
                    margin-top: 5rem;
                    margin-bottom: 5rem;
                    padding: 0;
                    
                    li{
                        display: flex;
                        align-items: center;
                        gap: 2rem;
                        border: none;
                        &:last-child{
                            display: none;
                        }
    
                        i{
                            font-size: 4rem;
                        }
                        .informacion-icono{
                            h3{
                                font-size: 2rem;
                                margin: 0;
                                text-align: left;
                            }
                            p{
                                margin: 0;
                                font-size: 1.2rem;
                            }
                        }
    
                    }
                }
            }
            
            @media(max-width: ($tablet)){
                ul{
                    display: grid;
                    grid-template-columns: repeat(2,1fr);
                    align-items: center;
                    list-style: none;
                    gap: 5rem;
                    margin-top: 5rem;
                    margin-bottom: 5rem;
                    padding: 0;
                    
                    li{
                        display: flex;
                        align-items: center;
                        gap: 2rem;
                        border: none;
                        &:last-child{
                            display: flex;
                        }
    
                        i{
                            font-size: 4rem;
                        }
                        .informacion-icono{
                            h3{
                                font-size: 2rem;
                                margin: 0;
                                text-align: left;
                            }
                            p{
                                margin: 0;
                                font-size: 1.2rem;
                            }
                        }
    
                    }
                }
            }

            @media(max-width: ($telefono)){
                ul{
                    display: grid;
                    grid-template-columns: repeat(1,1fr);
                    align-items: center;
                    list-style: none;
                    gap: 5rem;
                    margin-top: 5rem;
                    margin-bottom: 5rem;
                    padding: 0;
                    
                    li{
                        display: flex;
                        align-items: center;
                        gap: 2rem;
                        border: none;
                        &:last-child{
                            display: flex;
                        }
    
                        i{
                            font-size: 4rem;
                        }
                        .informacion-icono{
                            h3{
                                font-size: 2rem;
                                margin: 0;
                                text-align: left;
                            }
                            p{
                                margin: 0;
                                font-size: 1.2rem;
                            }
                        }
    
                    }
                }
            }

        }

        
        

        .google-maps{
            @include tablet{
                display: block;
                width: 100%;
                height: 450px;
                border: 10px solid $blanco !important;
                box-shadow: 0px 0px 5px #999;
            }
            display: none;
            
        }

        .texto-direccion{
            background-color: #00000088;
            text-align: left;
            width: 90%;
            padding: 2rem;
            margin: 0px auto;

            h3,p{
                @include tablet{
                    text-align: left !important;
                }
                text-align: center !important;
            }
            h3{
                color: $blanco;
                text-align: left;
                font-weight: 400;
                font-size: 2.2rem;
                
            }
            p{
                margin-top: -2rem;
                font-size: 1.5rem;
                color: $blanco;
                font-weight: 100;
            }
            
            ul{
                color: $blanco;
                margin-top: -2rem;
                list-style: square;
                
            }
            a{
                transition: 0.3s ease-in-out;
                overflow: hidden;
                &:hover{
                    -webkit-transform:scale(0.95);transform:scale(0.95);
                }
            }
            

        }
        
        
    }
    

    
}

