.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
    }
    img{
        width: 25rem;
        filter: drop-shadow(0px 0px 3px black);
    }
}

.navegacion{
    visibility: hidden;
    opacity: 0;
    transition: visibility linear, opacity 0.5s linear;
    margin-bottom: -15rem;
    @include tablet{
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
        margin-bottom: 0;
        
    }

    a{
        color: $blanco;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        text-shadow: 0px 0px 5px black;

        @include tablet{
            margin-right: 3rem;
            font-size: 2rem;
            transition: 0.5s ease-in-out;
            &:last-of-type{
                margin-right: 0;
            }
            background-image: linear-gradient(
                to right,
                #54b3d6,
                #54b3d6 50%,
                rgb(255, 255, 255) 50%
            );
            background-size: 200% 100%;
            background-position: -100%;
            display: inline-block;
            padding: 5px 0;
            position: relative;
            -webkit-background-clip: text;
            transition: all 0.3s ease-in-out;

            &:before{
                content: '';
                background: white;
                display: block;
                position: absolute;
                bottom: -3px;
                left: 0;
                width: 0;
                height: 3px;
                transition: all 0.3s ease-in-out;
            }
            &:hover{
                background-position: 0;
            }
               
            &:hover::before{
                width: 100%;
            }
        }
        
    }
}

.mobile-menu{
    @include tablet{
        display: none;

    }
    img{
        padding-top: 3rem;
        width: 5rem;
        float: right;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    margin-bottom: 0;

}