.no-webp .imagen-contacto{
    background-image: url(../img/casa11.png);
}
.webp .imagen-contacto{
    background-image: url(../img/casa11.webp);
}
.imagen-contacto{
    background-position: top;
    background-size: cover;
    padding: $separacion * 2 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    

    h2{
        font-size: 3.5rem;
        color: $blanco;
        background-color: transparent !important;
        font-weight: 400;
    }
    p{
        font-size: 1.5rem;
        color: $blanco;
        font-weight: 100;
    }
    a{
        width: 50%;
        transition: 0.5s ease-in-out;
        overflow: hidden;
        &:hover{
            -webkit-transform:scale(0.95);transform:scale(0.95);
        }
    }
    @media (max-width: ($desktop)){
        a{
            width: 100%;
            transition: 0.5s ease-in-out;
            overflow: hidden;
            &:hover{
                -webkit-transform:scale(0.95);transform:scale(0.95);
            }
        }
    }
    .texto-contacto{
        display: flex;
        align-items: center;
        width: 75%;
        gap: 10rem;
        .texto-participacion{
            width: 70%;
            h2{
                width: 100%;
                &::after{
                    content: '';
                    display: block;
                    width: 40rem;
                    height: .5rem;
                    background-color: $verde;
                    margin-top: 1rem;
                }
            }
        }

    }

    @media (max-width: ($desktop)){

        .texto-contacto{
            display: flex;
            flex-direction: column;
            width: 75%;
            gap: 0rem;
            .texto-participacion{
                width: 100%;
                h2{
                    text-align: center;
                    width: 100%;
                    &::after{
                        content: '';
                        display: block;
                        width: 100%;
                        height: .5rem;
                        background-color: $verde;
                        margin-top: 1rem;
                    }
                }
                p{
                    text-align: center;
                }
            }
    
        }
    }
}

.imagen-sombra{
    background-position:bottom;
    position: relative;
    z-index: 1;
    
}

.imagen-sombra::before{
    content: "";
    right: 0;
    left: 0;
    bottom: 0;
    top: 0;
    position: absolute;
    background: rgb(0 0 0 / 49%);
    z-index: -1;
}