html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    background-color: whitesmoke;
}
p {
    color: $negro;
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: $fuente_principal;
    margin: calc($separacion / 2) 0;
    font-weight: 400;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
    color: #6a6a6a;
}
h2 {
    font-size: 3.4rem;
    color: #6a6a6a;
    font-weight: bold;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

.swal-wide{
    width:850px !important;
}

h2{
    text-align: left;
    &::after{
        content: '';
        display: block;
        width: 40rem;
        height: .7rem;
        background-color: $rojo;
        margin-top: 1rem;
    }

    margin: 0;
    font-weight: 400;
}
@media (max-width: ($telefono)){
    h2{
        text-align: left;
        &::after{
            content: '';
            display: block;
            width: 30rem;
            height: .5rem;
            background-color: $rojo;
            margin-top: 1rem;
        }
    
        margin: 0;
        font-weight: 400;
    }
}
p{
    margin: 0;
    margin-top: 1rem;
    margin-bottom: 2rem;
    font-size: 1.5rem;
}
