.contenedor-catalogo{

    .construmas-picture{
        margin-top: 4.5rem;
    }
    
    .img-catalogo{
        margin-top: 3rem;
        width: 50% !important;
        filter: none !important;
    }
    
    .construmas-catalogo{
        width: 70% !important;
        filter: none !important;
    }
    

}

.texto-catalogo{
    text-align: center;
    font-size: 1.9rem;
    font-weight: 100;
    border-bottom: 5px solid $grisclaro;

    span{
        font-size: 1.5rem;
        font-style: italic;
        font-weight: 700;
    }

}