.texto-horarios{
    font-size: 1.5rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #999;
    @include tablet{
        margin-bottom: -1rem;
    }
    
}
.title-formulario{
    h2{
        text-align: center;
        &::after{
            margin: 0px auto;
        }
    }
    p{
        text-align: center;
    }
}
.contenedor-contacto{

    @include tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        margin-top: 3rem;
    }
    img{
        @include tablet{
            width: 70%;
            box-shadow: 0px 0px 5px #999;
        }
        width: 50%;
        margin: 0px auto;
        margin-bottom: 5rem;
        border: 5px solid white;
        box-shadow: 0px 0px 10px black;
        transition: 1s all ease-in-out;

        &:hover{
            filter: grayscale(1);
        }
    }
}

.formulario{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0;
    }
    textarea {
        resize: none;
    }
    fieldset{
        border: none;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
        text-align: center;
    }
    label{
        font-weight: 700;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: white;
        border: 2px solid rgb(235, 235, 235);
        border-radius: 0.5rem;
        margin-bottom: 1rem;
    }
    .boton-enviar{
        width: 50%;
        margin: 0px auto;
        padding: 1.5rem;
    }
    .g-recaptcha{
        display: flex;
        justify-content: center;
        margin-bottom: 3rem;
        border-bottom: 2px solid $gris;
        padding-bottom: 2rem;
        
    }
    
}

