.no-webp .nosotros{
    @include tablet{
        background-image: url(../img/casa10.webp);
    }
    background-image: url(../img/casa13.webp);

}

.webp .nosotros{ 
    @include tablet{
        background-image: url(../img/casa10.webp);
    }
    background-image: url(../img/casa13.webp);
    
    
}
.nosotros{
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    background-blend-mode: difference;   

}

.contenido-nosotros{
    @include tablet{
        @include grid(2,2rem);
        align-items: center;
        justify-items: center;
    }
    .imagen{
        border: 10px solid $blanco;
        box-shadow: 0px 0px 8px #999;
        width: 102%;
    }
    .texto-nosotros{
        margin-left: 2rem;
        p{
            font-weight: 100;
            font-size: 1.8rem;
        }
    }
}



.texto-nosotros blockquote{
    font-weight: 600;
    font-size: 2.7rem;
    margin: 0;
    padding: 1rem 0 1rem 0;
}