
.seccion-inferior{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;

}

@media (max-width: ($desktop)){
    .seccion-blog{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 2rem
    }
    .seccion-inferior{
        display: flex;
        flex-direction: column;
        
    
    }
}
@media (max-width: ($telefono)){
    .seccion-inferior{
        display: flex;
        flex-direction: column;
        
    
    }
    .seccion-blog{
        display: flex;
        flex-direction: column;
    }
}


.entrada-blog{
    margin-bottom: 2rem;
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }
}

.texto-entrada{
    a{
        color: $negro;
        margin-top: 2rem;
        display: block;
        @include tablet{
            margin-top: 0;
        }

    }
    h4{
        
        margin: 0;
        line-height: 1.4;
        &::after{
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
}